import React, { Component } from "react";
import { Container, Form } from 'react-bootstrap';
import { Link } from "react-router-dom";
import Constant from "../../util/constant_variables";
import MyStorage from '../../util/store';

export default class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userEmail: "",
            userPassword: "",
        };
        MyStorage.dispatch({ type: "" });
    }

    handleSubmit = async (e) => {
        e.preventDefault();

        if (this.state.userEmail !== '' && this.state.userPassword !== '') {

            const response = await this.sendLoginToServer();
            const json = await response.json();

            if (json.isLogin === "True") {
                console.log(" :", json);

                MyStorage.dispatch({ type: "LOGIN", data: { userId: json.userId, userEmail:this.state.userEmail, userPassword: this.state.userPassword } });
                alert("!");
                window.location.href = "/";
            } else {
                alert(" .  .");
                MyStorage.dispatch({ type: "LOGOUT" });
            }
        } else {
            alert('   !');
        }
    };

    async sendLoginToServer() {
        const data = {
            userEmail: this.state.userEmail,
            userPassword: this.state.userPassword,
        };
        try {
            const response = await fetch(Constant.serviceURL + '/login', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(data),
            });

            return response;
        } catch (error) {
            console.error('Error sending data to server:', error.message);
            throw error;
        }
    }

    render() {
        return (
            <Container style={{ marginTop: '40vh' }}>
                <div className="w-50 m-auto">
                    <Form onSubmit={(e) => this.handleSubmit(e)}>
                        <Form.Group className="mb-3">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="email"
                                autoFocus
                                onChange={(e) => { this.setState({ userEmail: e.target.value }) }} />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="password"
                                onChange={(e) => { this.setState({ userPassword: e.target.value }) }} />
                        </Form.Group>
                        <div className="text-center">
                            <button type="submit" className="btn color-btn"></button>
                        </div>
                    </Form>
                </div>
                <p>  ? <Link to='/SignupPage' className="color-btn"></Link></p>
            </Container>
        );
    }
}